
*{
    margin:0;
    padding: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    transition: ease-in-out 900ms ease-in-out;
}
h2{
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #434343;
}
.slider{
    position: absolute;
    width: 500px;
    height: 400px;
    overflow: hidden;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.slider .left-arrow{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: #fff;
    left: 50px;
    width:50px;
    height: 50px;
    color: #434343;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    cursor: pointer;
    

}
.slider .right-arrow{
    position: absolute;
    top: 50%;
    right: 50px;
    transform: translateY(-50%);
    background: #fff;
    width: 50px;
    height: 50px;
    color: #434343;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    cursor: pointer;


}
.slide{
    position: absolute;
    top:0;
    left:0;
    right: 0;
    bottom:0;
    
}
.slide img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120%;
}
